import React from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import About from '../containers/about';

type AboutPageProps = {};

const AboutPage: React.FunctionComponent<AboutPageProps> = () => {
  return (
    <Layout>
      <SEO
        title="About Us"
        description="Jo's Corner is a corner in the web where we can talk about software design, algorithms, machine learning run by Giorgio Zoppi. 
        Giorgio Zoppi is a seasoned Software Engineer and open source enthusiast."
      />

      <About />
    </Layout>
  );
};

export default AboutPage;
